.headerContainer {
  display: flex;
  flex-direction: row;
}

.headerText {
  font-family: "Acme", sans-serif;
  font-size: 68px;
  text-shadow: 4px 4px 4px #aaa;
  padding-top: 30px;
}

.logoContent {
  flex-grow: 2;
  justify-content: center;
  max-height: 145px;
}

.titleContent {
  display: flex;
  flex-grow: 4;
}

.messageContext {
  min-height: 75%;
  display: flex;
}

.messageHeader {
  font-size: 12px;
  padding: 0.3%;
  padding-top: 1%;
  width: 100%;
}

.messageText {
  font-size: 20px;
  min-height: 50%;
  flex-wrap: wrap;
  word-wrap: break-word;
  border-radius: 10px;
  padding: 10px;
  max-width: 50%;
}

.left {
  flex: 1;
  float: left;
  text-align: left;
  max-width: 50%;
  background-color: rgb(229, 249, 249);
}

.right {
  flex: 1;
  float: right;
  text-align: right;
  max-width: 50%;
  background-color: rgb(201, 238, 242);
}

.rightHeader {
  flex: 1;
  float: right;
  text-align: right;
  width: 100%;
}

.leftHeader {
  flex: 1;
  float: left;
  text-align: left;
  width: 100%;
}

.message {
  width: 100%;
  flex-direction: column;
  flex-wrap: wrap;
}

.messageDisplay {
  width: 100%;
  flex-direction: column;
  flex-wrap: wrap;
}

.messageblock {
  max-width: 100%;
}

.messageImage {
  width: auto;
  display: inline-block;
  height: 35px;
  padding-right: 7px;
}

body {
  font: 24px Helvetica;
  background: #999999;
  max-height: 99%;
}

#main {
  height: 730px;
  margin: 0px;
  padding: 0px;
  display: -webkit-flex;
  -webkit-flex-flow: row;
  flex-flow: row;
}
#main > article {
  margin: 4px;
  padding: 5px;
  border: 1px solid #8888bb;
  border-radius: 7pt;
  background: whitesmoke;
  overflow: auto;
  -webkit-flex: 3 1 60%;
  flex: 3 1 60%;
  -webkit-order: 2;
  order: 2;
}
#main > nav {
  margin: 4px;
  padding: 5px;
  border: 1px solid #8888bb;
  border-radius: 7pt;
  background: whitesmoke;
  -webkit-flex: 1 6 12%;
  flex: 1 6 12%;
  -webkit-order: 1;
  order: 1;
}

header {
  margin: 4px;
  padding: 5px;
  border: 1px solid #8888bb;
  border-radius: 7pt;
  background: whitesmoke;
  display: block;
  max-height: 170px;
}

footer {
  margin: 4px;
  padding: 5px;
  border: 1px solid #8888bb;
  border-radius: 7pt;
  background: whitesmoke;
  display: block;
  min-height: 50px;
}

@media all and (max-width: 640px) {
  #main, #page {
    -webkit-flex-flow: column;
    flex-flow: column;
  }
  #main > article, #main > nav, #main > aside {
    -webkit-order: 0;
    order: 0;
  }
  #main > nav, #main > header, footer {
    min-height: 150px;
    max-height: 40px;
  }
}
@font-face {
  font-family: "Acme";
  src: url("../fonts/Acme-Regular.ttf") format("opentype");
}
#components-dropdown-userSelector-dropdown-button .ant-dropdown-button {
  margin: 0 8px 8px 0;
}

.userImage {
  width: auto;
  display: inline-block;
  background: no-repeat center;
  background-size: cover;
  height: 27px;
  justify-content: flex-start;
}

.nameItem {
  flex: 4;
  padding-left: 20px;
}

.userItemMenu {
  max-height: 50%;
  display: flex;
}

.DropDownMenu {
  min-width: 250px;
  min-height: 43px;
}

.searchIcon {
  padding-left: 10px;
}

.sendMessageContext {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.inputMessage {
  flex: 4;
  padding-right: 10px;
  padding-left: 46px;
}

.selectorMessage {
  flex: 1;
  padding-left: 30px;
}

.sendButton {
  flex: 0;
  padding-left: 25px;
  padding-right: 25px;
}

.chatMemberImage {
  width: auto;
  display: inline-block;
  background: no-repeat center;
  background-size: cover;
  height: 50px;
  justify-content: flex-start;
}

.chatMemberName {
  flex: 4;
  padding-left: 30px;
  font-family: "Acme", sans-serif;
  font-size: 18px;
  padding-top: 1rem;
}

.chatMemberContext {
  max-height: 50%;
  display: flex;
}

.chatMemberTitle {
  font-family: "Acme", sans-serif;
  font-size: 36px;
  text-align: center;
}

.modalContext {
  display: flex;
  justify-content: space-between;
  min-height: 100px;
}

.searchInput {
  max-width: 50%;
}

.noData {
  width: 100%;
  text-align: center;
}

* {
  margin: 0px;
  padding: 0px;
}